}

.content-main .publication-listing-container {
  padding-top: 0;
}

.disease-button {
  background: #71c5e8;
  display: flex;
  justify-content: center;
  align-items: center;
}
.disease-button a {
  color: #001489;
  padding-right: 20px;
  text-decoration: none;
}
.disease-button .btn {
