}

.listing-disease-education.lt-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-basis: 100%;
}

.listing-disease-education .disease-heading {
  justify-content: space-between;
}

.disease-education .flex-heading img {
  margin-left: 20px;
}